<template>
    <div class="hello">
        <h1>{{ msg }}</h1>
        <p>
            For a guide and recipes on how to configure / customize this project,<br>
            check out the
            <a href="https://cli.vuejs.org" target="_blank" rel="noopener">vue-cli documentation</a>.
        </p>
        <h3>Installed CLI Plugins</h3>
        <ul>
            <li><a href="https://github.com/vuejs/vue-cli/tree/dev/packages/%40vue/cli-plugin-babel" target="_blank"
                   rel="noopener">babel</a></li>
        </ul>
        <h3>Essential Links</h3>
        <ul>
            <li ref="light"><a href="https://vuejs.org" target="_blank" rel="noopener">Core Docs</a></li>
            <li><a href="https://forum.vuejs.org" target="_blank" rel="noopener">Forum</a></li>
            <li><a href="https://chat.vuejs.org" target="_blank" rel="noopener">Community Chat</a></li>
            <li><a href="https://twitter.com/vuejs" target="_blank" rel="noopener">Twitter</a></li>
            <li><a href="https://news.vuejs.org" target="_blank" rel="noopener">News</a></li>
        </ul>
        <h3 ref="light2">Ecosystem</h3>
        <ul>
            <li><a href="https://router.vuejs.org" target="_blank" rel="noopener">vue-router</a></li>
            <li><a href="https://vuex.vuejs.org" target="_blank" rel="noopener">vuex</a></li>
            <li><a href="https://github.com/vuejs/vue-devtools#vue-devtools" target="_blank"
                   rel="noopener">vue-devtools</a></li>
            <li><a href="https://vue-loader.vuejs.org" target="_blank" rel="noopener">vue-loader</a></li>
            <li><a href="https://github.com/vuejs/awesome-vue" target="_blank" rel="noopener">awesome-vue</a></li>
        </ul>
        <button @click="showMethods" ref="showTour">show tour</button>
        <tourguide :tourConfigList="tourConfigList" :show="show" @closeTour="closeTour"></tourguide>
    </div>
</template>

<script>
    export default {
        name: 'HelloWorld',
        data(){
            return {
                show: true,
            }
        },
        props: {
            msg: String,
            tourConfigList: [],
        },
        methods: {
            closeTour(){
                this.show = false;
            },
            showMethods(){
                this.show = true;
            }
        },
        mounted(){
            this.$nextTick( () => {
                this.tourConfigList = [];
                this.tourConfigList.push( {
                    displayPosition: "left-top",
                    elements: [this.$refs.light],
                    axisType: "x",
                    desc: "This is a core document"
                });

                this.tourConfigList.push( {
                    displayPosition: "right-top",
                    elements: [this.$refs.light2],
                    axisType: "y",
                    desc: "title"
                });
                this.tourConfigList.push( {
                    displayPosition: "right-down",
                    elements: [this.$refs.showTour],
                    axisType: "y",
                    desc: "open wizard"
                })
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h3 {
        margin: 40px 0 0;
    }

    ul {
        list-style-type: none;
        padding: 0;
    }

    li {
        display: inline-block;
        margin: 0 10px;
    }

    a {
        color: #42b983;
    }
</style>
